.hidden, [hidden] {
  display: none;
}

.block {
  display: block;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.margin-auto {
  margin: auto;
}

.center {
  text-align: center;
}

.full-width {
  width: 100%;
}

.half-width {
  width: 50%;
}

.full-height {
  height: 100%;
}

.full-block {
  width: 100%;
  height: 100%;
}

.flex {
  display: flex;
}

.flex-row {
  display: flex;
  flex-flow: row nowrap;
}

.flex-column {
  display: flex;
  flex-flow: column nowrap;
}

.flex-center,
.flex-row-center {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

.flex-column-center {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.absolute,
.absolute-top-left {
  position: absolute;
  top: 0;
  left: 0;
}

.absolute-top-right {
  position: absolute;
  top: 0;
  right: 0;
}

.absolute-bottom-left {
  position: absolute;
  bottom: 0;
  left: 0;
}

.absolute-bottom-right {
  position: absolute;
  bottom: 0;
  right: 0;
}

.relative {
  position: relative;
  top: 0;
  left: 0;
}

.text-allipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}

button.round-button, a.round-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  padding: 10px;
  color: white;
  border: 1px solid white;
  border-radius: 50%;
  outline-width: 0;
  background-color: #24292e;
}

button.round-button + .round-button, a.round-button + .round-button {
  margin-left: 5px;
}

.color-navy {
  color: #001f3f;
}

.color-blue {
  color: #0074D9;
}

.color-aqua {
  color: #7FDBFF;
}

.color-teal {
  color: #39CCCC;
}

.color-olive {
  color: #3D9970;
}

.color-green {
  color: #2ECC40;
}

.color-lime {
  color: #01FF70;
}

.color-yellow {
  color: #FFDC00;
}

.color- {
  color: #FF851B;
}

.color-orange {
  color: #FF4136;
}

.color-maroon {
  color: #85144b;
}

.color-fushia {
  color: #F012BE;
}

.color-purple {
  color: #B10DC9;
}

.color-silver {
  color: #DDDDDD;
}

.color-gray {
  color: #AAAAAA;
}

.color-black {
  color: #111111;
}

html, body {
  height: 100%;
  width: 100%;
  -webkit-text-size-adjust: none;
  /* prevent webkit from resizing text to fit */
  -webkit-touch-callout: none;
  /* prevent callout to copy image, etc when tap to hold */
  -webkit-user-select: none;
  /* prevent copy paste, to allow, change 'none' to 'text' */
  touch-action: manipulation;
  font-family: Calibri, "Trebuchet MS", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
}